.container {
  padding-top: 40px;
  padding-bottom: 40px;
  color: white;
  background-color: rgb(51, 51, 51);
}
.heading {
  text-align: center;
  margin-bottom: 20px;
}
.listGroup {
  background-color: transparent;
  color: white;
}
.listItem {
  text-align: center;
  background-color: transparent;
